.connect {
    background-color: #6d6969;
    &_heading {
        color: #c4c4c4;
        font-size: 25px;
        padding-top: 50px;
    }
    &_form {
        color: #c4c4c4;
        display: flex;
        flex-direction: column;
        &_button {
            color: #c4c4c4;
            width: 20vw;
            margin: 50px auto;
            background-color: #4a4a4a;
            padding: 15px;
            text-align: center;
            font-size: 18px;
            @media screen and (max-width: 575px) {
                width: auto;
            }
        }
    }
    & input {
        background-color: transparent;
        margin-top: 25px;
        width: 80%;
        border-radius: 20px;
        padding: 15px;
        outline: none;
        color: #c4c4c4;
        font-size: 18px;
    }
}