.footer {
    background-color: #c4c4c4;
    padding: 40px 0;
    &_wrapper {
        display: flex;
        align-items: center;
        justify-content: space-around;
        @media screen and (max-width: 575px) {
            flex-direction: column;
            text-align: center;
        }
    }
    &_link {
        font-size: 20px;
        color: #4a4a4a;
        text-decoration: none;
    }
    &_media {
        display: flex;
        justify-content: space-between;
        list-style: none;
        @media screen and (max-width: 575px) {
            flex-direction: column;
        }
    }
    &_list {
        list-style: none;
    }
    &_item {
        margin-right: 10px;
        list-style: none;
        @media screen and (max-width: 575px) {
            margin-top: 10px;
        }
    }
    &_item-phone {
        list-style-image: url("../img/general/phone.png");
    }
    &_item-mail {
        list-style-image: url("../img/general/mail.png");
    }
}