.main {
    background-color: #a0a0a0;
    padding: 30px 0;
    @media screen and(max-width: 575px) {
        display: flex;
        flex-direction: column;
    }
    &_heading {
        font-size: 25px;
    }
    &_top {
        display: flex;
        justify-content: space-between;
        @media screen and(max-width: 575px) {
            display: flex;
            flex-direction: column;
        }
    }
    &_search {
        align-items: center;
        position: relative;
        width: 30%;
        border-radius: 9px;
        @media screen and (max-width: 575px) {
            width: auto;
            margin-top: 50px;
        }
        & input[type = search] {
            width: 100%;
            height: 42px;
            border-radius: 20px;
            padding: 0 70px;
            outline: none;
            background-color: #c4c4c4;
            border-right: transparent;
            color: #a0a0a0;
        }
        &_button {
            position: absolute;
            top: 0;
            left: 15px;
            width: 42px;
            height: 42px;
            border: transparent;
            padding: 4px;
            border-radius: 9px;
            outline: none;
            background-color: transparent;
        }
    }

    &_content {
        display: flex;
        justify-content: space-between;
        margin-top: 40px;
        width: 90%;
        @media screen and(max-width: 1024px) {
            display: flex;
            flex-direction: column;
        }
    }
    &_typeoflife {
        height: 55vh;
        border: 1px solid #4a4a4a;
        padding: 20px;
        @media screen and(max-width: 575px) {
            width: auto;
            height: auto;
            padding: 5px;
            margin-bottom: 10px;
        }
        @media screen and (min-width: 575.99px) and (max-width: 900px) {
            height: auto;
        }
        &_wrapper {
            position: relative;
            &:after {
                content: url("../../../img/general/select-arrow.png");
                display: block;
                position: absolute;
                top: 50%;
                right: 10%;
                width: 10px;
                height: auto;
                border-radius: 20px;
                @media screen and (min-width: 320.99px) and (max-width: 375px) {
                    right: 23%;
                }
                @media screen and (min-width: 375.99px) and (max-width: 425px) {
                    right: 38%;
                }
                @media screen and (min-width: 425.99px) and (max-width: 490px) {
                    right: 41%;
                }
                @media screen and (min-width: 490.99px) and (max-width: 575px) {
                    right: 50%;
                }
                @media screen and (min-width: 575.99px) and (max-width: 650px) {
                    right: 52%;
                }
                @media screen and (min-width: 650.99px) and (max-width: 770px) {
                    right: 60%;
                }
                @media screen and (min-width: 770.99px) and (max-width: 900px) {
                    right: 60%;
                }
                @media screen and (min-width: 900.99px) and (max-width: 1024px) {
                    right: 71%;
                }
            }
        }
    }

    &_heading3 {
        font-size: 18px;
        font-weight: 700;
        margin-top: 24px;
    }
    &_select {
        width: 250px;
        height: 30px;
        padding: 5px;
        margin-top: 17px;
        outline: none;
        border-radius: 20px;
        background: #c4c4c4;
        color: #e5e5e5;
        appearance: none;
        & option {
            color: #e5e5e5;
            font-size: 12px;
        }
    }
    &_button {
        display: block;
        margin: 40px auto 0;
        padding: 20px 40px;
        outline: none;
    }
    &_cards {
        display: grid;
        grid-template-columns: auto auto auto;
        @media screen and (max-width: 575px) {
            grid-template-columns: auto;
        }
        @media screen and (min-width: 575.99px) and (max-width: 900px) {
            grid-template-columns: auto auto;
            margin-top: 10px;
        }
        @media screen and (min-width: 900.99px) and (max-width: 1024px) {
            margin-top: 10px;
        }
    }

    &_card {
        width: 210px;
        border: 1px solid #4a4a4a;
        margin-left: 60px;
        margin-bottom: 50px;
        @media screen and (max-width: 575px) {
            width: auto;
        }
    }

    &_image {
        width: 45px;
        height: 45px;
        margin: 10px auto;
        border-radius: 50%;
        background: #c4c4c4;
    }
    &_name {
        background: #4a4a4a;
        text-align: center;
        white-space: pre-wrap;
        &_heading {
            font-weight: 400;
            font-size: 14px;
            padding-top: 17px;
        }
        &_paragraph {
            width: 20%;
            padding: 5px;
            font-weight: 400;
            font-size: 14px;
        }
        &_button {
            padding: 4px;
            margin-top: 10px;
            margin-left: auto;
        }
    }

    &_link {
        display: block;
        width: 4%;
        margin-left: 60%;
        text-align: center;
        color: #4a4a4a;
        font-size: 18px;
    }
}