.add__bot {
    background-color: #c4c4c4;
    padding: 20px 0;
    &_button {
        display: flex;
        align-items: center;
        margin: 0 auto 20px;
        padding: 2% 10%;
    }
    &_span {
        font-size: 22px;
        color: #4a4a4a;
    }
    &_form {
        display: flex;
        flex-direction: column;
        width: 50%;
        margin: auto;
        background-color: #e5e5e5;
        border: 2px solid #4a4a4a;
        box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
        padding: 4%;
        @media screen and (max-width: 425px) {
            width: auto;
        }
        &_col {
            align-items: center;
            justify-content: space-between;
            margin-top: 30px;
            & span {
                display: inline-block;
                width: 20%;
                font-size: 20px;
                @media screen and (max-width: 425px) {
                    font-size: 12px;
                }
            }
        }
        & input {
            width: 70%;
            padding: 2% 0;
        }
    }
    &_textarea {
        width: 70%;
        padding: 70px;
        @media screen and (max-width: 900px) {
            padding: 40px;
        }
    }
    &_send-button {
        display: block;
        margin: 60px auto 0;
        padding: 2% 5%;
        font-size: 20px;
        color: #4a4a4a;
        background-color: #c4c4c4;
    }
    &_dark-grey {
        background-color: #a0a0a0;
        padding: 30px;
    }
    &_form-wrapper {
        display: flex;
        position: relative;
        width: 70%;
        margin-left: 21%;
        margin-top: 40px;
        &:after {
            content: url("../../../img/general/select-arrow.png");
            display: block;
            position: absolute;
            top: 40%;
            right: 10%;
            width: 10px;
            height: auto;
            border-radius: 20px;
            @media screen and (min-width: 320.99px) and (max-width: 375px) {
                right: 23%;
            }
            @media screen and (min-width: 375.99px) and (max-width: 425px) {
                right: 38%;
            }
            @media screen and (min-width: 425.99px) and (max-width: 490px) {
                right: 41%;
            }
            @media screen and (min-width: 490.99px) and (max-width: 575px) {
                right: 50%;
            }
            @media screen and (min-width: 575.99px) and (max-width: 650px) {
                right: 52%;
            }
            @media screen and (min-width: 650.99px) and (max-width: 770px) {
                right: 60%;
            }
            @media screen and (min-width: 770.99px) and (max-width: 900px) {
                right: 60%;
            }
            @media screen and (min-width: 900.99px) and (max-width: 1024px) {
                right: 71%;
            }
        }
    }
    &_select {
        width: 100%;
        padding: 4%;
        appearance: none;
        outline: none;
    }
}
