.header {
    background-color: $gray_color;
    padding: 10px;
    &_top {
        display: flex;
        justify-content: space-between;
        margin-left: 60px;
        margin-top: 30px;
        @media screen and(max-width: 900px) {
            flex-direction: column;
        }
    }
    &_list {
        display: flex;
        justify-content: space-between;
        list-style: none;
        @media screen and(max-width: 900px) {
            flex-direction: column;
            width: auto;
        }
    }
    &_item {
        border: 1px solid $border_color;
        margin-right: 30px;
    }
    &_link {
        text-decoration: none;
        font-size: 18px;
        font-weight: 500;
        color: $button_text;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 150px;
        height: 45px;
        padding: 10px;
        @media screen and(max-width: 575px) {
            width: auto;
        }
        @media screen and(min-width: 425px) and (max-width: 900px) {
            margin: auto;
        }
        @media screen and (min-width: 900.99px) and (max-width: 1024px) {
            width: 120px;
        }
    }
    &_body {
        margin-top: 100px;
        margin-left: 70px;
        display: flex;
        justify-content: space-between;
        @media screen and(max-width: 900px) {
            flex-direction: column;
        }
    }
    &_article {
        position: relative;
        width: 550px;
        height: 180px;
        background-color: $article_color;
        //padding: 10px;
        @media screen and(max-width: 900px) {
            width: 80%;
        }
        &:before {
            content: "";
            position: absolute;
            bottom: 0;
            width: 0;
            height: 0;
            border-bottom: 210px solid $gray_color;
            border-right: 50px solid transparent;
            @media screen and(max-width: 1024px) {
                display: none;
            }
        }
        &:after {
            content: "";
            position: absolute;
            right: 0;
            bottom: 0;
            width: 0;
            height: 0;
            border-top: 210px solid $gray_color;
            border-left: 50px solid transparent;
            @media screen and(max-width: 1024px) {
                display: none;
            }
        }
    }
    &_bot {
        width: 55%;
        background-color: $gray_color;
        @media screen and(max-width: 575px) {
            width: 86%;
        }

        @media screen and(min-width: 575.99px) and (max-width: 900px) {
            width: 83%;
        }
    }
    &_paragraph {
        font-size: 18px;
        color: #6d6969;
        text-align: justify;
        padding: 35px;
        @media screen and (max-width: 320px) {
            font-size: 10px;
        }
        @media screen and (min-width: 320.99px) and (max-width: 575px) {
            font-size: 12px;
            width: auto;
        }
    }
}
.logo {
    width: 15%;
    padding: 20px 25px;
    background-color: $logo_color;
    border-radius: 50%;
    text-align: center;
    font-size: 12px;
    margin-right: 10%;
    @media screen and(max-width: 575px) {
        margin-bottom: 10px;
    }
}
