.profile__header {
    &_top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        text-align: center;
        @media screen and(max-width: 900px) {
            flex-direction: column;
        }
    }
    &_top-link {
        font-size: 22px;
        color: #4a4a4a;
        text-decoration: none;
        margin: 5% auto;
    }
}