.advantage {
    background-color: #c4c4c4;
    padding: 35px;
    @media screen and (max-width: 575px) {
        display: flex;
        flex-direction: column;
    }
    &_cols {
        display: flex;
        justify-content: space-between;
        @media screen and (max-width: 575px) {
            display: flex;
            flex-direction: column;
        }
    }
    &_col {
        position: relative;
        width: 250px;
        text-align: center;
        &:before {
            content: "";
            position: absolute;
            top: 30%;
            left: -45%;
            width: 30%;
            height: 3px;
            background: #4a4a4a;
            @media screen and (max-width: 1024px) {
                display: none;
            }
        }
        &:after {
            content: "";
            position: absolute;
            top: 30%;
            right: -45%;
            width: 30%;
            height: 3px;
            background: #4a4a4a;
            @media screen and (max-width: 1024px) {
                display: none;
            }
        }
        &:nth-child(2) {
            &:before, &:after {
                display: none;
            }
        }
        &_rect {
            width: 190px;
            height: 190px;
            margin: auto;
            background: url("../img/general/rect.png") no-repeat;
            background-size: cover;
        }
    }
    &_paragraph {
        font-size: 20px;
        color: #c4c4c4;
        padding: 60px 40px 0 40px;
    }
}